    .expense-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        /* box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25); */            /* This was moved to Card.css*/
        padding: 0.5rem;
        margin: 1rem 0;
        border-radius: 12px;
        background-color: #0d0d0d;
    }

    .expense-item__description {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        align-items: flex-end;
        flex-flow: column-reverse;
        justify-content: flex-start;
        flex: 1;
    }


    .expense-item h2 {
        /* color: #3a3a3a; */
        color: rgb(219, 214, 214);
        font-size: 1rem;
        flex: 1;
        margin: 0 1rem;
        /* color: black; */
    }

    .expense-item__price {
        font-size: 1rem;
        font-weight: bold;
        color: white;
        background-color: #084e35;
        border: 1px solid white;
        padding: 0.5rem;
        border-radius: 12px;           
        color: black;

    }

    @media (min-width: 580px) {
        .expense-item__description {
            flex-direction: row;
            align-items: center;
            justify-content: flex-start;
            flex: 1;
        }

        .expense-item__description h2 {
            font-size: 1.25rem;
        }

        .expense-item__price {
            font-size: 1.25rem;
            padding: 0.5rem 1.5rem;
        }
    }